body{
  background-color: #17181f;
  color: #ddd;
}
*{
  box-sizing: border-box;
}
main{
  max-width:300px;
  margin: 20px auto;
}
form{
  border: 3px solid #30313D;
  border-radius: 10px;
  padding:5px;
  display:flex;
}
form input[type="text"]{
  background: none;
  border:0;
  display: block;
  color:#fff;
  padding:0 8px;
  width:100%;
}
form button{
  background-color: #61DAFB;
  border:0;
  border-radius: 6px;
  cursor: pointer;
}
div.task{
  background-color: #30313D;
  border-radius: 10px;
  margin-top: 5px;
  padding: 5px 9px;
  display: flex;
  transition: opacity .3s linear;
  align-items: center;
}
div.task svg{
  height: 20px;
margin-right: 6px;
fill:#61DAFB;
}

div.tast.done{
  opacity: 0.3;
}
div.task-name,div.task form{
  flex-grow: 1;
}
div.task span{
  position: relative;
}
div.task span:before{

content:'';
display: block;
width: 0;
height: 1px;
background-color: #ddd;
position: absolute;
top:12px;
transition: width .1s linear;
}

div.task.done span:before{
  width: 100%;
}

h1,h2{
  text-align: center;
}
div.checkbox{
  display: flex;
  align-items: center;
  
}
button.trash{
  background: none;
  border: none;
  display: flex;
  cursor: pointer;
}

button.trash svg{
  fill:#888;
  height: 14px;
}

div.task input{
  border:2px solid #555;
  border-radius: 5px;
  padding: 2px 4px;
}